<template>
    <div id="app">
    <header class="mui-bar mui-bar-nav" style="background:#4fae38;color:#fff">
            <h1 style="float: left">EYE</h1>  
            <h1 class="mui-title" style="color:#fff;font-size: 20px"></h1>
            <div style="float: right">
                <router-link to="/Home">
                    <button type="button" class="mui-btn mui-btn-link" style="color:#fff">
                        <span class="mui-icon mui-icon-back"></span>
                        
                    </button>
                </router-link>
            </div>
        </header><br>
        <div style="margin-left:0px;width: 300px;">
           <h3></h3>
           <p><b>1:</b>,50,</p>
           <p><b>11->7/10:</b>,50,</p>
        </div>
      <div id="allmap" ref="allmap"></div>
     <router-view></router-view>
    </div>
  </template>
   
  <script>
  export default {
    name: 'App',
    methods:{
      map(){
        let map =new BMap.Map(this.$refs.allmap);// Map
        map.centerAndZoom(new BMap.Point(113.454042,34.773858), 17);// ,
        map.addControl(new BMap.MapTypeControl({//
          mapTypes:[
            BMAP_NORMAL_MAP,
            BMAP_HYBRID_MAP
          ]}));
        map.setCurrentCity("");//  
        map.enableScrollWheelZoom(true);//
      }
   
    },
    mounted(){
      this.map()
   
    }
  }
  </script>
   
  <style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    
    color: #2c3e50;
    margin-top: 40px;
  }
  #allmap{
    height: 430px;
    overflow: hidden;
   
  }
  </style>
 


