<template>
    <div class="app-goodlist">
        <!-- 1  -->
        <div class="goods-item" v-for="item of list" :key="item.i">
            <!-- 1.1  -->
            <img src="img2/3.png" alt="">
            <!-- 1.2  -->
            <h4>{{item.lname}}</h4>
            <!-- 1.3  -->
            <h4 class="now">{{item.price}}</h4>
        </div>
        <mt-button size="large" type="primary" @click="loadMore"></mt-button>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                list:[],
                pno:1  //
              
            }
        },
        created () {
            //console.log(123)
            //1. url
            var url="getGoodList?";
            //2.ajax 
            this.axios.get(url).then(result=>{
                //3.
                this.list=result.data.data
                //4.list
                console.log(this.list)
            })
           
       console.log(this.$route.query.id); 
        },
      
        methods: {
            loadMore(){
                if(this.hasMore==false){
                   this.$toast(".....");
                    return;
                }
                this.pno++;
                //console.log(123)
                //1. url
                var url="getGoodList";
                //2. pno=2;
               // url+="?pno=2";
               
                
                    
                    url+="?pno="+this.pno;
                    this.axios.get(url).then(result=>{
                       var rows=this.list.concat(result.data.data)
                       this.list=rows;
                       if(this.pno>=result.data.pageCount){
                        this.hasMore=false;
                       }
                   });
            }
        }
    }
</script>
<style>
    .app-goodlist{
        display: flex;   /****/
        flex-wrap: wrap;  /****/
        justify-content: space-between;  /****/
    }
    .app-goodlist .goods-item{
        width:49%;
        border:1px solid #ccc;   
        margin:2px 0;
        padding:2px;
        display:flex;         
        flex-direction: column;            /****/
        min-height: 245px;
    }
    .app-goodlist .goods-item img{
        width:100%;
    }
</style>



